    .ResumeContainer {
        display: grid;
        width: 90%;
        height: 98vh;
        margin: 10px 5% 0px 5%;
        grid-template-columns:1fr 9fr auto;
        grid-template-rows:1fr 5fr 1fr; 
        text-align: center;
        grid-gap:.4%; 
    }
    .ResumeContainer .LeftBox {
        display: none;
    }
.ResumeContainer * {
    text-decoration: none;
}
.Main {
    grid-row: 1/5;
    grid-column: 2 / auto;
}
.Connection {
    /* border-radius:0px 10px 10px 0px;  */
    justify-self: center;
    padding: 10px;
}
.ProfileName {
    background: #5d6d7c;
    /* color: #fbb023; */
    height: 100%;
    color: #fdd000;
    padding: 2% 1% 1% 1%;
    border-top-left-radius:10px; 
}
/* -------------Media Queries------------- */ 
@media only screen and(max-width: 1250px){

    .ResumeContainer {
        width: 92%;
        margin: 15px 4% 0px 4%;
    } 
}
@media only screen and (max-width: 1200px){
    .ResumeContainer {
        width: 94%;
        margin: 20px 3% 0px 3%;
    } 
}

@media only screen and (max-width: 1150px){
    .ResumeContainer {
        width: 96%;
        grid-template-columns:1fr 8fr auto;
        /* margin: 20px 2% 0px 2%; */
        grid-template-rows:1fr 4fr 1fr; 
    } 
}
@media only screen and (max-width:1100px){
    .ResumeContainer {
        grid-template-columns:1fr 7fr auto;
        /* margin: 20px 2% 0px 2%; */
        /* grid-gap: .6%;  */
    } 
    .ServicesContainer {
        /* display: grid; */
        grid-template-columns: repeat(2,1fr) !important;
    }
    .Contact .PersonalDeep {
        grid-template-columns: repeat(2,1fr) !important;
    
    }
    .PortfolioContent {
        grid-template-columns: repeat(2,1fr) !important;
     }
}
