  /* ================ The Timeline ================ */
  
  .timeline {
    position: relative;
    width: 100%;
  /*   margin: 0 auto; */
    /* margin-top: 20px; */
    padding: 0px !important;
    list-style-type: none;
  }
  .timeline:after {
    position: absolute;
    top: 0;
    content: ' ';
    display: block;
    width: 6px;
    height: 100%;
    margin-left: -3px;
    background: rgb(80,80,80);
    background: linear-gradient(to bottom, rgba(80,80,80,0) 0%, rgb(80,80,80) 8%, rgb(80,80,80) 92%, rgba(80,80,80,0) 100%);
    
    z-index: 5;
  }
  .timeline li {
    height: 20%;
  }
  .timeline li:after {
    content: "";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
  
  /* .direction-l {
    position: relative;
    width: 300px;
    float: right;
    text-align: right;
  } */
  
  .direction-r {
    position: relative;
    /* width: 300px; */
    float: left;
    text-align: start;
    margin: 5px auto 5px 35px !important;
    /* margin-left: 35px !important; */
  }
  .direction-r::after {
      /* text-align: start; */
  }
  
  .flag-wrapper {
    position: relative;
    display: inline-block;
    text-align: start;
  }
  
  .flag {
    position: relative;
    display: inline;
    background: rgb(238, 235, 235);
    /* background: yellow; */
    padding: 6px 10px;
    border-radius: 5px;
    
    font-weight: 580;
    text-align: center;
    box-shadow: 1px 1px 1px rgba(0,0,0,0.15), 0 0 1px rgba(0,0,0,0.15);
    -webkit-box-shadow: 1px 1px 1px rgba(0,0,0,0.15), 0 0 1px rgba(0,0,0,0.15);
    -moz-box-shadow: 1px 1px 1px rgba(0,0,0,0.15), 0 0 1px rgba(0,0,0,0.15);
  }
 .flag:before {
    position: absolute;
    top: 50%;
    /* left: 10%; */
    /* right: -40px; */
    /* right: 100px; */
    content: ' ';
    display: block;
    width: 12px;
    height: 12px;
    margin-top: -10px;
    /* margin-left: -2px; */
    background: #fff;
    border-radius: 10px;
    border: 4px solid rgb(255,80,80);
    z-index: 10;
  }
  
  .direction-r .flag:before {
    left: -44px;
  }
  
  .direction-r .flag:after {
    content: "";
    position: absolute;
    right: 100%;
    top: 50%;
    height: 0;
    width: 0;
    margin-top: -8px;
    border: solid transparent;
    border-right-color: rgb(248,248,248);
    border-right-color: rgb(238, 235, 235);
    border-width: 8px;
    pointer-events: none;
  }
  
  .time-wrapper {
    display: inline;
    
    line-height: 1em;
    background: lightgray;
    font-size: 0.66666em;
    color: rgb(250,80,80);
    vertical-align: middle;
  }
  .direction-r .time-wrapper {
    float: right;
  }
  
  .time {
    display: inline-block;
    padding: 4px 6px;
  }
  
  .desc {
    /* margin: 1em 0.75em 0 0; */
    /* margin: 10px !important; */
    color: gray;
    font-size: 1em;
    /* background: lightgray; */
    margin: 10px 10px 20px 10px !important;
    padding: 20px !important;
    border: 1px solid lightgray;
    border-radius: 10px;
  }
  
  .direction-r .desc {
    /* margin: 1em 0 0 0.75em; */
  }
  
